import React, { useState, useEffect } from 'react'

function ChangingText() {
    // const ConstData = ["HTML", "CSS", "SCSS", "&"]
    const ConstData = ["HTML", "CSS", "SCSS", "React", "NodeJS", "MongoDB", "Github", "JS", "REST API", "GraphQL", "MySQL", "PHP", "Photoshop", "&"]


    const [state, setState] = useState("HTML")
    // useEffect(() => {

    // }, [rerender])


    useEffect(() => {
        let data = ConstData.map((item) => {
            // console.log(item)
            let pom3 = ""
            let i = 0
            while (i < item.length) {
                pom3 = pom3 + "."
                i++
            }

            return item + "     ....." + pom3
        })

        data = data.toString()
        data = data.replace(/,/g, '')
        data = data.split("")
        ChangingElements(data)

        // Testing()
    }, [])

    function ChangingElements(data) {
        let Concat = ""
        data.map((item2, id) => {

            setTimeout(function () {
                if (item2 !== ".") {
                    Concat = Concat + item2
                    document.getElementById("test2").innerHTML = Concat
                }
                else {
                    Concat = Concat.slice(0, -1)
                    document.getElementById("test2").innerHTML = Concat
                }
                if (item2 === "&") {
                    ChangingElements(data)
                }
            }, id * 5);
            return null
        })


    }

    // function Testing() {
    //     ConstData.map((item, id) => {
    //         setTimeout(function () {
    //             document.getElementById("test2").innerHTML = item
    //             if (item === "&")
    //                 Testing()
    //         }, id * 800);
    //         return null
    //     })

    // }



    return (
        <div className="htmlChanging">
            {/* {`<${state}/>`} */}
            {console.log("test")}
            {"<"}<span id="test2"></span>{"/>"}
            {/* {Testing()} */}
            {/* <div onMouseDown={Testing}>asd</div> */}
            {/* <div >{Testing()}</div> */}
            {/* {`<Photosop />`} */}
        </div>
    )
}

export default ChangingText
